:root{
    --hr-color: rgba(0, 0, 0, 0.09);
}

body{
    background-image: linear-gradient(to top, white, rgb(250, 250, 250));
}
main{
    padding-bottom: 90px;
}

video{
    width: 100%;
    margin-bottom: 100px;
}
.video-banner-landing{
    position: relative;
}
.video-banner-landing .inside-video{
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
}
.video-banner-landing h1{
    text-align: center;
    line-height: 45px;
    color: rgb(255, 255, 255);
    text-shadow: 0 3px black;
    font-size: 4em;
    margin: 0 0 50px 0;
}
.video-banner-landing p{
    color: white;
    font-size: .5em;
    margin: 0 0 0 -120px;
}
.video-banner-landing p span{
    color: rgb(219, 132, 19);
    text-shadow: 0 .7px rgb(0, 0, 0);
    font-size: 1.2em;
    margin: 0;
}
.inside-video button{
    border: none;
    border-radius: 30px;
    max-width: 350px;
    min-width: 200px;
    height: 50px;
    background-color: black;
    color: white;
    font-weight: 700;
    transition: all 1s;
}
.inside-video button:hover{
    scale: 1.1;
    font-size: 1.1em;
}

.blur-all{
    filter: blur(1.5px);
}

.images-container{
    display: flex;
    flex-flow: wrap;
    gap: 15px;
    justify-content: center;
}

.images-container img{
    box-shadow: 0 2px 10px 2px rgba(0, 0, 0, 0.297);
    border-radius: 8px;
    width: auto;
}

.images-container .aside-image-container{
    display: flex;
    flex-flow: column;
    gap: 15px;
}

